        resume  : "Nous sommes ravis de partager avec vous que React Native 0.69 est la premire version  prendre en charge React 18. React 18 a apport de nombreuses amliorations , comme de nouveaux crochets tels que useId. De plus, React 18 inclut de nouvelles fonctionnalits de simultanit telles que la prise en useTransitioncharge complte de Suspense. Sur React Native 0.69, React 18 est activ par dfaut. Toutefois, si vous n'avez pas migr vers la nouvelle architecture, vous ne pourrez tirer parti que des fonctionnalits qui n'utilisent pas le rendu simultan et les fonctionnalits simultanes. La nouvelle architecture a t construite avec le rendu simultan  l'esprit, mais nous ne pouvons pas ajouter cette prise en charge pour l'ancienne architecture. Vous pouvez en savoir plus sur la prise en charge de React 18 dans React Native ici. Avant cette version, Hermes et React Native taient sortis sparment. Cela a conduit  une confusion sur quelle version d'Hermes est compatible avec quelle version de React Native. Pour remdier  ce problme,  partir de React Native 0.69, nous expdierons une version compatible d'Hermes avec React Native. Faire ce changement rendra l'utilisation d'Hermes dans React Native beaucoup plus stable.",
